<template>
  <div id="myComment">
    <div class="myComment_input">
      <input class="input" @focus="focus" v-model="input_value" @keydown="clickC" placeholder="说点什么评论下吧">
    </div>
    <div class="input_shade" @click="cancel" v-show="textShow"></div>
    <div class="input_area" v-show="textShow">
      <div class="header">
        <p class="header_cancel" @click="cancel">取消</p>
        <p class="header_submit" @click="submit">提交</p>
      </div>
      <textarea v-model="text_data" class="text_area" placeholder="发表你的评论吧" ref="text_area"></textarea>
    </div>
    <div class="myComment_like" @click="like"><img v-if="myComment_like" src="../common/images/已赞@2x.png"><img v-else
                                                                                                               src="../common/images/赞@2x.png">
    </div>
    <div class="myComment_collect" @click="collect"><img v-if="myComment_collect" src="../common/images/已收藏@2x.png"><img
      v-else
      src="../common/images/收藏@2x.png">
    </div>
    <div class="myComment_share" @click="share"><img src="../common/images/分享@2x.png"></div>
    <div class="share_area" v-show="dis_share">
      <div class="share_div">
        <div class="share_content">
          <div style="display: inline-block" @click="goShare(0)">
            <img src="../common/images/微信@2x.png">
            <p>微信</p>
          </div>
          <div style="display: inline-block" @click="goShare(1)">
            <img src="../common/images/朋友圈@2x.png">
            <p>朋友圈</p>
          </div>
          <div style="display: inline-block" @click="goShare(2)">
            <img src="../common/images/QQ@2x.png">
            <p>QQ</p>
          </div>
        </div>
        <div class="share_cancel" @click="shareCancel">
          取消
        </div>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  name: 'myComment',
  props: {
    liked: {
      type: Boolean,
      default: false
    },
    collected: {
      type: Boolean,
      default: false
    }
  },
  data () {
    return {
      myComment_like: this.liked,
      myComment_collect: this.collected,
      input_value: '',
      textShow: false,
      text_data: '',
      dis_share: false
    }
  },
  mounted () {
  },
  methods: {
    clickC (e) {
      if (e.keyCode !== 13) {
        return
      }
      this.$emit('clickC', this.input_value)
      this.input_value = ''
    },
    focus () {
      this.textShow = true
      this.$nextTick(() => {
        this.$refs.text_area.focus()
      })
    },
    cancel () {
      this.textShow = false
      this.text_data = ''
    },
    submit () {
      if (this.text_data.length < 1) {
        this.$Dialog.Toast({ content: '写点东西再提交吧' })
        return
      }
      this.textShow = false
      // 接下进行发表评论
      this.$emit('update', this.text_data)
      this.text_data = ''
    },
    like () {
      if (this.myComment_like) {
        this.myComment_like = false
        this.$Dialog.Toast({ content: '取消点赞成功', title_show: false })
      } else {
        this.myComment_like = true
        this.$Dialog.Toast({ content: '点赞成功', title_show: false })
      }
      this.$emit('like', this.myComment_like)
    },
    collect () {
      if (this.myComment_collect) {
        this.myComment_collect = false
        this.$Dialog.Toast({ content: '取消收藏成功', title_show: false })
      } else {
        this.myComment_collect = true
        this.$Dialog.Toast({ content: '收藏成功', title_show: false })
      }
      this.$emit('collect', this.myComment_like)
    },
    share () {
      this.dis_share = true
    },
    shareCancel () {
      this.dis_share = false
    },
    goShare (i) {
      this.shareCancel()
      // 接口文档地址https://qydev.weixin.qq.com/wiki/index.php
    }
  }
}
</script>

<style scoped>
  #myComment {
    position: fixed;
    bottom: 0;
    height: 3.333333rem;
    background-color: #FFFFFF;
    width: 100%;
  }

  #myComment div img {
    width: 1.8rem;
    margin: 0.5rem;
  }

  #myComment .myComment_like {
    display: inline-block;
    text-align: center;
    position: absolute;
    right: 6.5rem;
    top: 0.3rem;
  }

  #myComment .myComment_collect {
    display: inline-block;
    text-align: center;
    position: absolute;
    right: 3.5rem;
    top: 0.3rem;
  }

  #myComment .myComment_share {
    display: inline-block;
    text-align: center;
    position: absolute;
    right: 0.5rem;
    top: 0.3rem;
  }

  #myComment .myComment_input {
    display: inline-block;
    position: absolute;
    top: 0.666666rem;
  }

  #myComment .myComment_input .input {
    height: 2rem;
    width: 13rem;
    margin: 0 1rem;
    padding: 0 0.5rem;
    border: 0;
    font-size: 1rem;
    background-color: #F5F5F6;
  }

  #myComment .input_area {
    height: 10rem;
    background-color: white;
    z-index: 1;
    position: fixed;
    bottom: 0;
    width: 100%;
  }

  #myComment .input_area .header {
    border-bottom: 0.5px solid #CCCCCC;
    padding: 0.2rem;
    height: 2rem;
  }

  #myComment .input_area .header .header_cancel {
    display: inline-block;
    text-align: left;
    width: 11rem;
    padding-left: 1rem;
    margin: 0 auto;
  }

  #myComment .input_area .header .header_submit {
    display: inline-block;
    text-align: right;
    width: 11rem;
    padding-right: 1rem;
    margin: 0 auto;
  }

  #myComment .input_area .text_area {
    width: 100%;
    height: 8rem;
    border: 0;
    outline: none;
  }

  #myComment .input_shade {
    position: fixed;
    bottom: 8rem;
    top: 0;
    left: 0;
    width: 100%;
    background-color: rgba(0, 0, 0, 0.2);
  }

  #myComment .share_area {
    position: fixed;
    top: 0;
    left: 0;
    bottom: 0;
    width: 100%;
    background-color: rgba(0, 0, 0, 0.65);
  }

  #myComment .share_area .share_div {
    position: absolute;
    bottom: 0;
    height: 13rem;
    width: 100%;
    background-color: #FFFFFF;
  }

  #myComment .share_area .share_div .share_content {
    position: absolute;
    top: 0;
    width: 100%;
    left: 0;
    height: 9rem;
    border-bottom: 2px solid #EEEEEE;
  }

  #myComment .share_area .share_div .share_content div img {
    width: 3.33333rem;
    margin: 2rem 2.8rem 0 2rem;
  }

  #myComment .share_area .share_div .share_content div p {
    text-align: center;
    margin: 0 2.8rem 0 2rem;
  }

  #myComment .share_area .share_div .share_cancel {
    position: absolute;
    bottom: 0;
    width: 100%;
    left: 0;
    text-align: center;
    height: 3.8rem;
    line-height: 3.8rem;
  }
</style>



