<template>

  <div id="newS" ref="newS">
    <div class="newS_title">{{data.title}}</div>
    <div class="newS_time">{{dataFormat(data.time)}} {{data.views}}阅读</div>
    <div class="newS_content">{{data.content}}</div>
    <my-comment-area :loadingShow="loadingShow" :comment_data="comment_data" :bottomShow="bottomShow"
                     :CommentSubmit="CommentSubmit"></my-comment-area>
    <my-comment @clickC="sentC" @update="update" @like="like" @collect="coll"></my-comment>
  </div>
</template>

<script>
import myComment from '../../../components/myComment'
import myCommentArea from '../../../components/myCommentArea'

import myUtil from '../../../common/myUtil'

let NewS
export default {
  name: 'newS',
  components: {
    myCommentArea,
    myComment
  },
  data () {
    return {
      bottomShow: false,
      data: this.$route.params.news_data,
      loadingShow: false,
      comment_data: [
        {
          name: '用户名用户名',
          text: '评论内容评论内容评论内容评论内容评论内容评论内容评论内容评论内容',
          time: '12-10 17:00',
          portrait: ''
        }, {
          name: '用户名用户名',
          text: '评论内容评论内容评论内容评论内容评论内容评论内容评论内容评论内容评论内容评论内容评论内容评论内容评论内容评论内容评论内容评论内容评论内容评论内容评论内容评论内容评论内容评论内容评论内容评论内容评论内容评论内容评论内容评论内容评论内容评论内容评论内容评论内容',
          time: '12-10 16:00',
          portrait: ''
        }, {
          name: '用户名用户名',
          text: '评论内容评论内容评论内容评论内容评论内容评论内容评论内容评论内容',
          time: '10-09 12:34',
          portrait: ''
        }
      ],
      CommentSubmit: 0,
      currentPage: 0,
      pageSize: 3
    }
  },
  mounted () {
    this.init()
    // this.query()
  },
  destroyed () {
    NewS.removeEventListener('scroll', this.scroll, false)
  },
  methods: {
    init () {
      NewS = this.$refs.newS
      NewS.addEventListener('scroll', this.scroll, false)
      console.log(NewS.scrollTop, NewS.clientHeight, NewS.scrollHeight)
      if (NewS.clientHeight === NewS.scrollHeight) {
        this.query()
      }
    },
    // query () {
    //   this.getNewsInfo()
    // },
    // 获取资讯详情
    getNewsInfo () {
      this.$http.getNewsInfo({
        id: 1231
      }).then((res) => {})
    },
    // 分页查询评论
    findByPageC () {
      this.$http.findByPageC({
        newsId: 12312, // 资讯id
        page: this.currentPage,
        size: this.pageSize
      }).then((res) => {})
    },
    // 新增评论
    addComment () {
      this.$http.addComment({
        newsId: 12312, // 资讯id
        userId: 123123, // 用户id
        username: '名字', // 用户名
        userHead: '',
        content: '',
        authKey: ''
      }).then((res) => {})
    },
    // 资讯点赞 不区分是谁点赞和收藏会造成重复点赞和收藏统计问题
    praise (val) {
      this.$http.praise({
        id: 12312, // 资讯内容id
        isCollect: val // 0: 取消 1：收藏
      }).then((res) => {})
    },
    // 资讯收藏
    collect (val) {
      this.$http.collect({
        id: 12312, // 资讯内容id
        isPraise: val // 0: 取消 1：点赞
      })
    },
    dataFormat (time) {
      return myUtil.formatTime(time)
    },
    scroll () {
      // console.log('你进行了滑动')
      console.log(NewS.scrollTop, NewS.clientHeight, NewS.scrollHeight)
      let nowH = NewS.scrollTop + NewS.clientHeight
      let maxH = NewS.scrollHeight
      if (nowH + 1 > maxH) {
        // 显示正在加载
        this.loadingShow = true
        setTimeout(() => {
          this.query()
        }, 3000)
      }
    },
    query () {
      // 取消正在加载
      this.loadingShow = false
      let time = myUtil.getTime('MM-DD HH:mm')
      console.log(time)
      let newVal = {
        name: '用户名用户名',
        text: '评论内容评论内容评论内容评论内容评论内容评论内容评论内容评论内容',
        time: time,
        portrait: ''
      }
      // this.$Dialog.Loading({ state: 'end' })
      // 这个判断更改为获取的数据长度为0
      if (this.currentPage > 3) {
        // 显示滑到低提示
        this.bottomShow = true
      } else {
        this.comment_data.push(newVal)
        this.currentPage += 1
        this.$nextTick(() => {
          NewS = this.$refs.newS
          if (NewS.clientHeight === NewS.scrollHeight) {
            this.query()
          }
        })
      }
    },
    sentC (value) {
      console.log('你进行的评论，评论的内容为:' + value)
    },
    update (val) {
      let time = myUtil.getTime('MM-DD HH:mm')
      let message = {
        name: '用户名用户名',
        text: val,
        time: time,
        portrait: ''
      }
      this.comment_data.push(message)
      this.CommentSubmit = new Date().getTime()
    },
    like (val) {
      val = val ? 1 : 0
      this.praise(val)
    },
    coll (val) {
      val = val ? 1 : 0
      this.collect(val)
    }
  }
}
</script>

<style scoped>
  #newS {
    position: fixed;
    bottom: 3.33333rem;
    top: 3rem;
    overflow-y: auto;
  }

  #newS .newS_title {
    font-size: 1.3rem;
    color: #333333;
    padding: 1rem;
    font-weight: 600;
  }

  #newS .newS_time {
    color: #999999;
    padding: 0 1rem 1rem 1rem;
  }

  #newS .newS_content {
    padding: 1rem;
    color: #333333;
    font-size: 1.1rem;
    font-weight: 500;
  }

  #newS .newS_foot {
    position: absolute;
    bottom: 0;
    width: 100%;
    height: 4rem;
    background-color: #e2eaea;
  }
</style>



