<template>
  <div id="myCommentArea">
    <my-title :title="'全部评论'" :more_show="false"></my-title>
    <div class="comment" v-for="(item,index) in comment_data" :key=index>
      <div class="comment_portrait">
        <img :src="imgSrc">
      </div>
      <div class="comment_right">
        <div class="comment_name">{{item.name}}</div>
        <div class="comment_text" ref="comment_text">{{item.text}}</div>
        <p v-if="item.moreShow" @click="showP(index)">{{item.showData}}<img :src="item.showImg"></p>
        <div class="comment_time">{{timeChange(item.time)}}</div>
      </div>
    </div>
    <div v-show="loadingShow&&!bottomShow" class="comment_loading">
      <div class="loading one"></div>
      <div class="loading two"></div>
      <div class="loading three"></div>
      <div class="loading four"></div>
      <p>正在加载...</p>
    </div>
    <div v-if="bottomShow" class="comment_bottom"><p>已加载全部评论</p></div>
  </div>
</template>

<script>
import myTitle from './myTitle'

import myUtil from '../common/myUtil'

export default {
  name: 'myCommentArea',
  components: {
    myTitle
  },
  props: {
    comment_data: Array,
    bottomShow: {
      type: Boolean,
      default: false
    },
    loadingShow: {
      type: Boolean,
      default: false
    },
    CommentSubmit: {
      type: Number,
      default: 0
    },
    imgSrc: {
      type: String,
      default: require('../common/images/客服.jpg')
    }
  },
  computed: {},
  watch: {
    CommentSubmit: {
      handler (newVal, oldVal) {
        if (newVal > 0 && oldVal !== undefined) {
          this.$nextTick(() => {
            this.query()
          })
        }
      },
      immediate: true
    }
  },
  data () {
    return {
      loadingT: ''
    }
  },
  mounted () {
    this.query()
    this.loadingTime()
  },
  destroyed () {
  },
  methods: {
    timeChange (time) {
      let T = myUtil.formatTime('2019-' + time)
      return T
    },
    loadingTime () {
      let loading = this.$refs.loading
      for (let i in loading) {
        loading[i].style.animation = 'animation: loading 0.5s ease ' + i * 0.1 + 's infinite alternate;'
      }
    },
    query () {
      for (let i of this.comment_data) {
        i.moreShow = false
      }
      let commentText = this.$refs.comment_text
      for (let i in this.comment_data) {
        if (commentText.length < this.comment_data.length) {
          return
        }
        if (commentText[i].scrollHeight > commentText[i].clientHeight) {
          this.comment_data[i].moreShow = true
          this.comment_data[i].showData = '展开'
          this.comment_data[i].showImg = require('../common/images/下@2x.png')
          this.$set(this.comment_data, i, this.comment_data[i])
        }
      }
      // console.log(this.comment_data)
    },
    showP (i) {
      let commentText = this.$refs.comment_text
      if (this.comment_data[i].showData === '展开') {
        this.comment_data[i].showData = '收起'
        this.comment_data[i].showImg = require('../common/images/箭头上@2x.png')
        this.$set(this.comment_data, i, this.comment_data[i])
        commentText[i].style.webkitLineClamp = 100
      } else {
        this.comment_data[i].showData = '展开'
        this.comment_data[i].showImg = require('../common/images/下@2x.png')
        this.$set(this.comment_data, i, this.comment_data[i])
        commentText[i].style.webkitLineClamp = 4
      }
    }
  }
}
</script>

<style scoped>
  #myCommentArea {
    border-top: 10px solid #F6F7F8;
    margin-top: 1rem;
  }

  #myCommentArea .comment {
    position: relative;
    border-bottom: 0.5px solid #F6F7F8;
  }

  #myCommentArea .comment .comment_portrait {
    position: absolute;
    display: inline-block;
    width: 2rem;
    height: 2rem;
    border-radius: 1rem;
    margin: 1rem;
    background-color: #CCCCCC;
  }

  #myCommentArea .comment .comment_portrait img {
    width: 2rem;
    /*height: 2rem;*/
  }

  #myCommentArea .comment .comment_right {
    display: inline-block;
    margin-left: 4rem;
    width: 21rem;
  }

  #myCommentArea .comment .comment_right p {
    text-align: left;
    font-size: 0.8rem;
    margin: 0 auto;
    color: blue;
  }

  #myCommentArea .comment .comment_right p img {
    position: relative;
    width: 1.233333rem;
    top: 0.3rem;
  }

  #myCommentArea .comment .comment_right .comment_name {
    font-size: 0.8rem;
    color: black;
    margin: 1rem 0;
    font-weight: 500;
  }

  #myCommentArea .comment .comment_right .comment_text {
    font-size: 0.9rem;
    font-weight: 500;
    padding: 0 0.5rem 0 0;
    -webkit-line-clamp: 4;
    text-overflow: ellipsis;
    display: -webkit-box;
    overflow: hidden;
    -webkit-box-orient: vertical;
    word-break: break-word;
  }

  #myCommentArea .comment .comment_right .comment_time {
    font-size: 0.8rem;
    color: black;
    text-align: right;
    padding: 1rem;
  }

  #myCommentArea .comment_loading {
    padding: 1rem 8rem;
  }

  #myCommentArea .comment_loading p {
    margin: 0 auto;
    text-align: center;
  }

  #myCommentArea .comment_loading .loading {
    width: 1rem;
    height: 1rem;
    margin: 0.5rem;
    display: inline-block;
    transform: scale(1.5);
    border-radius: 0.5rem;
    background-color: #ff0000;
  }

  #myCommentArea .comment_loading .one {
    animation: loading 0.5s ease 0.1s infinite alternate;
  }

  #myCommentArea .comment_loading .two {
    animation: loading 0.5s ease 0.2s infinite alternate;
  }

  #myCommentArea .comment_loading .three {
    animation: loading 0.5s ease 0.3s infinite alternate;
  }

  #myCommentArea .comment_loading .four {
    animation: loading 0.5s ease 0.4s infinite alternate;
  }

  #myCommentArea .comment_bottom {
    padding: 0.5rem;
    background-color: #F6F7F8;
  }

  #myCommentArea .comment_bottom p {
    text-align: center;
    font-size: 0.8rem;
  }

  @keyframes loading {
    from {
      transform: scale(1)
    }

    to {
      transform: scale(0.6);
    }

  }
</style>



